import { Basis } from '../../../basis';
import { ButtonKinds } from './button-kinds';
import { ButtonSizes } from './button-sizes';
import { ButtonIconPlaces } from './button-icon-places';

class Button extends Basis {
  constructor(data = { label: 'libell√©', tagName : '', id : '', classes : [], attributes : {}, kind : null, type : null, title : null, disabled : false, size : 'md', icon : null, iconPlace : 'only' }) {
    super(data);
    this._label = data.label;
    this._tagName = data.tagName || 'button';
    this._kind = data.kind || ButtonKinds.PRIMARY;
    this._type = data.type;
    this._title = data.title;
    this.disabled = data.disabled;
    this.size = data.size;
    this.icon = data.icon;
    this._iconPlace = data.iconPlace;
  }

  set label (value) {
    this._label = value;
  }

  get label () {
    return this._label;
  }

  get tagName () {
    return this._tagName;
  }

  set id (value) {
    this._id = value;
  }

  get id () {
    return this._id;
  }

  get classList () {
    return this._classList;
  }

  get attributes () {
    return this._attributes;
  }

  set type (value) {
    this._type = value;
  }

  get type () {
    return this._type;
  }

  set kind (value) {
    if (!Object.values(ButtonKinds).includes(value)) return;
    this._kind = value;
  }

  get kind () {
    return this._kind;
  }

  set title (value) {
    this._title = value;
  }

  get title () {
    return this._title;
  }

  set disabled (value) {
    this._disabled = value;
  }

  get disabled () {
    return this._disabled;
  }

  set size (value) {
    if (!Object.values(ButtonSizes).includes(value)) return;
    this._size = value;
  }

  get size () {
    return this._size;
  }

  set icon (value) {
    this._icon = value;
  }

  get icon () {
    return this._icon;
  }

  set iconPlace (value) {
    if (!Object.values(ButtonIconPlaces).includes(value)) return;
    this._iconPlace = value;
  }

  get iconPlace () {
    return this._iconPlace;
  }

  get classes () {
    const classes = [
      'fr-btn',
      ... super.classes
    ];

    if (this._size && this._size !== 'md') classes.push(`fr-btn--${this._size}`);
    if (this._kind && this._kind !== ButtonKinds.PRIMARY) classes.push(`fr-btn--${this._kind}`);

    return classes;
  }

  get html () {
    return `<${this._tagName} ${this.includeClasses()} ${this.includeAttributes()}>${this._label}</${this._tagName}>`;
  }
}

export { Button };
